.wrapper {
  position: relative;
  width: 100%;
  background: var(--grey-color-3);
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-sizing: border-box;
  box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.13);
  cursor: pointer;
}



.title {
  color: var(--text-additional-color);
  margin-bottom: 20px;
}

.block {
  width: 100%;
  padding-top: 20px;
  border-top: 1px solid var(--grey-color-9);
  text-align: end;
}

.icon {
  position: absolute;
  top: 20px;
  right: 20px;
  border: none;
  outline: none;
  background: none;
  width: 36px;
}

.link {
  width: 400px;
  text-decoration: none;
}

@media screen and (max-width: 938px) {
  .link {
    width: 100%;
    max-width: 500px;
  }
}